import { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import { Card } from "../../card/Card";
import { getProducts } from "../../../api/getProducts";
import Spinner from '../../spinner/Spinner'
import DocumentTitle from "../DocumentTitle";
import styles from './SearchProducts.module.scss'


const ListProducts = () => {
    const inputValueFromRedux = useSelector((state) => state.inputValue.inputValue);
    const [data, setData] = useState([]);
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
        setIsLoading(true);
        getProducts()
            .then(responseData => {
                setData(responseData);
                setIsLoading(false);
            })
            .catch(error => {
                console.error("     :", error);
                setIsLoading(false);
            });
    }, [inputValueFromRedux]);

    const filteredData = data.filter(item => item.name.toLowerCase().includes(inputValueFromRedux.toLowerCase()));

    
    return (
        <>
            <DocumentTitle title={`: ${inputValueFromRedux}`}/>
            
                <section className={styles.cardsSectionWrapper}>
                    <h1 className={styles.cardsSectionHeadline}> </h1>
                    {isLoading ? (
                        <Spinner />
                    ) : (
                        <ul className={styles.cardsListWrapper}>
                            {filteredData.length > 0 ? (
                                <>
                                    {filteredData.map((item, index) => (
                                        <Card
                                            key={index}
                                            itemNo={item.itemNo}
                                            name={item.name}
                                            price={item.price}
                                            nameCloudinary={item.nameCloudinary[0]}
                                            isLot={item.category}
                                        />
                                    ))}
                                </>
                            ) : (
                                <p> ,    </p>
                            )}
                        </ul>
                    )}
                </section>
        </>
    );
}

export default ListProducts;
