import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import styles from './Header.module.scss';

const BurgerMenu = ({toggleBar}) => {
  const [isOpen, setIsOpen] = useState(false);

  const toggleMenu = () => {
    setIsOpen(!isOpen);
    toggleBar()
  };

  return (
    <nav className={styles.wrapperMenu}>
      <button className={`${styles.toggleButton} ${isOpen ? styles.cross : ''}`} onClick={toggleMenu}>
        <div className={styles.bar}></div>
        <div className={styles.bar}></div>
        <div className={styles.bar}></div>
      </button>
      {isOpen && (
        <ul className={styles.menuList}>
          <Link to="/" className={styles.menuItem}></Link>
          <Link to="/about-us" className={styles.menuItem}> </Link>
          <Link to="/categories" className={styles.menuItem}></Link>
          <Link to="/blog" className={styles.menuItem}></Link>
          <Link to="/contacts" className={styles.menuItem}></Link>
        </ul>
      )}
    </nav>
  );
};

export default BurgerMenu;


