import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import { Form, Field, ErrorMessage, Formik } from "formik"
import { object, string } from "yup"
import EyeClosed from "../logIn/eye/EyeClosed";
import EyeOpen from "../logIn/eye/EyeOpen";
import { FormButton } from "../button/Button";
import registrationUser from "../../api/registrationUser"
import PropTypes from "prop-types"
import styles from "./Registration.module.scss"


function Registration({ headline, to, isAdmin }){
  const [showPassword, setShowPassword] = useState(false);
  const togglePasswordVisibility = () => setShowPassword(!showPassword);
  const [showError, setShowError] = useState("");
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const validationSchema = object().shape({
    firstName: string()
      .required("   '  ")
      .matches(/[a-zA-Z--']/, "   ' a-z, A-Z, -, -")
      .min(2, "'    2  25 ")
      .max(25, "'    2  25 "),
    lastName: string()
      .required("   '  ")
      .matches(/[a-zA-Z--']/, "    a-z, A-Z, -, -")
      .min(2, "    2  25 ")
      .max(25, "    2  25 "),
    login: string()
      .required("   '  ")
      .matches(/[a-zA-Z--]/, "    a-z, A-Z, 0-9")
      .min(6, "     6 ")
      .max(10, "     10 "),
    email: string()
      .email("   ")
      .required("     '  "),
    password: string()
      .required("   '  ")
      .min(7, "    7  30 ")
      .max(30, "    7  30 ")
      .matches(/[a-zA-Z0-9]/, "   : a-z, A-Z, 0-9"),
    telephone: string()
      .matches(/\+380\d{3}\d{2}\d{2}\d{2}/, "   ")
  })

  const handleUserRegistration = (firstName, lastName, login, email, password, telephone, birthdate) => {
    dispatch(registrationUser(firstName, lastName, login, email, password, telephone, birthdate, isAdmin))
      .then(() => {
        navigate(to);
      })
      .catch((error) => {
        if (error.response && error.response.data) {
          if (error.response.data.login === "Login must be between 3 and 10 characters") {
            setShowError("    3  10 ");
          } else if (error.response.data.login === "Allowed characters for login is a-z, A-Z, 0-9.") {
            setShowError("    a-z, A-Z, 0-9");
          } else if (error.response.data.password === "Allowed characters for password is a-z, A-Z, 0-9.") {
            setShowError("Allowed characters for password is a-z, A-Z, 0-9.");  
          } else if (error.response.data.message.includes("already exists")) {
            setShowError("      ");
          }
        }
      });
  };


  return(
    <section className={styles.windowWrapper}>
      <div className={styles.window}>
        <h1 className={styles.headline}>{headline}</h1>
        <p className={`${styles.text} ${styles.headlineText}`}>   </p>

        <Formik 
          initialValues={{
            firstName: "",
            lastName: "",
            login: "",
            email: "",
            password: "",
            telephone: "",
            birthdate: ""
          }}
          onSubmit={(values, { setSubmitting }) => {
            handleUserRegistration(values.firstName, values.lastName, values.login, values.email, values.password, values.telephone, values.birthdate);
            setSubmitting(false);
          }}
          validationSchema={validationSchema}
        >

          {({ isSubmitting }) => (
            <Form className={styles.form}>

              <Field name="firstName">
                {({ field, meta }) => (
                    <input
                      {...field}
                      id="firstName"
                      className={ meta.touched && meta.error ? styles.inputAttention : styles.input }
                      placeholder="'"
                    />
                )}
              </Field>
              <Field name="lastName">
                {({ field, meta }) => (
                  <input
                    {...field}
                    id="lastName"
                    className={ meta.touched && meta.error ? styles.inputAttention : styles.input }
                    placeholder=""
                />
                )}
              </Field>
              <Field name="login">
                {({ field, meta }) => (
                  <input
                    {...field}
                    id="login"
                    className={ meta.touched && meta.error ? styles.inputAttention : styles.input }
                    placeholder=""
                />
                )}
              </Field>
              <Field name="email">
                {({ field, meta }) => (
                  <input
                    {...field}
                    type="email"
                    id="email"
                    className={ meta.touched && meta.error ? styles.inputAttention : styles.input }
                    placeholder="Email"
                  />
                )}
              </Field>
              <Field name="password">
                {({ field, meta }) => (
                  <div htmlFor="password" className={`${styles.passwordWrapper} ${styles.label}`}>
                    <input
                      {...field}
                      type={showPassword ? "text" : "password"}
                      id="password"
                      className={ meta.touched && meta.error ? styles.inputAttention : styles.input }
                      placeholder=""
                    />
                    <div
                      type="button"
                      onClick={togglePasswordVisibility}
                      className={styles.iconButton}
                    >
                      {showPassword === false ? <EyeClosed /> : <EyeOpen />}
                    </div>
                  </div>
                )}
              </Field>
              <Field name="telephone">
                {({ field, meta }) => (
                  <input
                    {...field}
                    type="tel"
                    id="tel"
                    className={ meta.touched && meta.error ? styles.inputAttention : styles.input }
                    placeholder=""
                  />
                )}
              </Field>
              <Field name="birthdate">
                {({ field, meta }) => (
                  <input
                    {...field}
                    type="date"
                    id="date"
                    className={ meta.touched && meta.error ? styles.inputAttention : styles.input }
                    placeholder=" "
                  />
                )}
              </Field>

              <FormButton type="submit"
                className={styles.buttonStyle}
                disabled={isSubmitting}
                text=""
                width="300px"/>
              <div className={styles.errorsWrapper}>
                {showError ? <p className={showError && styles.textAttention}>{showError}</p> : null}
                <ErrorMessage name="firstName" component="p" className={styles.textAttention}/>
                <ErrorMessage name="lastName" component="p" className={styles.textAttention}/>
                <ErrorMessage name="login" component="p" className={styles.textAttention}/>
                <ErrorMessage name="email" component="p" className={styles.textAttention}/>
                <ErrorMessage name="password" component="p" className={styles.textAttention}/>
                <ErrorMessage name="telephone" component="p" className={styles.textAttention}/>
              </div>
            </Form>
          )}
        </Formik>
      </div>
    </section>
  )
}


Registration.propTypes = {
  headline: PropTypes.string.isRequired,
  to: PropTypes.string.isRequired
};

export default Registration